import React, { useState } from "react";
import { gen16CodeId, getSvgSymbols } from "../stores/Utils";
import { SvgIcon_Cmpt } from ".";
import "./AccountLabelDetail.css";

const Svg = SvgIcon_Cmpt();


const AccountLabelDetail  = ({ data, color, labelField, infoField}) => {
	
	const [open, setOpen] = useState(true);
	const [show, setShow] = useState(true);

	const openInfo = (v) => {
		if(!show){
			setOpen(true)
			setTimeout(() => {
				setShow(true)
			}, 50);
		}
		else{
			setShow(false)
			setTimeout(() => {
				setOpen(false)
			}, 50);
		}
	}
	
	const mouseLeave = (v) => {
		if(show){
			setShow(false)
			setTimeout(() => {
				setOpen(false)
			}, 50);
		}
	}

	const mouseIn = (v) => {
		if(!show){
			setOpen(true)
			setTimeout(() => {
				setShow(true)
			}, 50);
		}
	}

	let maxW = data?.[infoField]?.length;

	return (
		<div className="lbl_dts _dsplFlx" style={{color:color}} >
			
			<div onMouseLeave={mouseLeave}>
			{data?
				<div className="icon_info" onClick={openInfo}>
					{/* <Svg name={open?"Xclose":"alert"} color={"#2f2f2f"} size={18}/>*/}
					{open?
					<div className={"boxOpenInfo"} style={{opacity:show?1:0, maxWidth:  maxW*9}}>
						<div className={"boxInfo"} >
							<span>
							{data?.[infoField]}
							</span>
						</div>
					</div>
					:null}
				</div>
				:null}
				<span onMouseMove={mouseIn}>
					{data?.[labelField]}
				</span>
			</div>
		</div>
	)
}

export default AccountLabelDetail;


